<template>
	<div>
		<ul>
			<li v-for="(item,index) in meiTuList" :key="index">
				<img :src="item.url" alt="">
			</li>
		</ul> 
	</div>
</template>
<script>
	export default{
		data(){
			return {
				page:28,
				meiTuList:[]
			}
		},
		created(){
			this.axiosMeitu();
		},
		methods:{
			axiosMeitu(){
				this.$axios.get(`https://www.apiopen.top/meituApi?page=${this.page}`)
				.then(res=>{
					console.log(res.data);
					this.meiTuList = res.data.data;
				})
				.catch(err=>{
					console.log(err)
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	ul{
		li{
			margin-bottom: 10px;
			img{
				width: 100%;
			}	
		}
	}
	
</style>


